import React, { useCallback } from 'react'
import { useHistory } from 'react-router'
import { ReactComponent as Logo } from 'src/assets/icon/icon-logo.svg'
// import { ReactComponent as Service } from 'src/assets/icon/icon-service.svg'
import { LangSelect, Service } from 'src/components'
import { isMobileAgent } from 'src/utils'
import { useToggle } from 'src/hooks'
import './Header.scss'

interface HeaderProps {
  lang: string,
  locale: any
}

const Header: React.FC<HeaderProps> = ({ lang, locale }) => {
  const toggle = useToggle()
  const history = useHistory()
  const isMobile = isMobileAgent()
  const handleClick = useCallback(
    () => {
      history.push('/')
    }, [history])
  
  const toService = useCallback(() => {
      toggle.update()
      // history.push('/service')
    }, [toggle])

  return (
    <div className='header-wrap'>
      <div> <Logo className='logo' onClick={handleClick} /> </div>
      <div>
        {!isMobile && <a className='account' href='https://gow.one/' target="_blangk" rel="noopener noreferrer" ><span className="icon">+</span> {locale.createAccount}</a>}
        <p className='connect-service' onClick={toService}>
          {/* <Service className='header-icon' /> */}
          <span>{locale.onlineService}</span>
        </p>
        <LangSelect />
      </div>
      {
       toggle.current && <Service locale={locale} lang={lang} toggle={toggle}/>
      }
    </div>
  )
}

export default Header