import React, { useLayoutEffect, memo } from 'react'
import { BrowserRouter, Switch, Route, RouteProps, withRouter } from 'react-router-dom'
import { observer, inject } from 'mobx-react'
import routes from 'src/routes'
import { calcFontSize, parseSearch } from 'src/utils'
import './global.scss'


const App: React.FC = observer(({ matchLang, lang, location: { search } }: any) => {
  const langRouter = parseSearch(search)?.lang

  useLayoutEffect(() => {
    // 
    if (langRouter) {
      matchLang(langRouter)
    } else if (localStorage.language) {
      // 
      matchLang(localStorage.language)
    } else {
      // 
      matchLang('cn')
    }
  }, [matchLang, langRouter])

  calcFontSize()
  return (
    <BrowserRouter>
      <Switch>
        {routes.map((route: RouteProps, index: number) => {
          return (
            <Route
              key={index}
              exact={true}
              {...route}
            />
          )
        })}
      </Switch>
    </BrowserRouter>
  )
})

const mapStateToProps = ({ localeStore: { lang, locale, matchLang } }: any) => {
  return {
    lang,
    locale,
    matchLang
  }
}

export default withRouter(inject(mapStateToProps)(memo(App)))