import React, { useCallback, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { observer, inject } from 'mobx-react'
import {Header, Footer, FooterMobile} from 'src/Layout'
import { NavBar, SearchInput } from 'src/components'
import Categories from './Categories'
import { isMobileAgent } from 'src/utils'
import './Home.scss'


const Home: React.FC<any> = observer(({ lang, locale, allCate }) => {
  const isMobile = isMobileAgent()
  const history = useHistory()
  const [word, setWord] = useState('')

  // 
  const handleEnter = useCallback((e) => {
    if (e.keyCode === 13  || e.type === 'click') {
      history.push(`/search?word=${word}&lang=${lang}`)
    }
  }, [history, word, lang])


  return (
    <div className='home-page-wrap'>
      {!isMobile && <Header locale={locale} lang={lang} />}
      {isMobile && <NavBar />}
      <SearchInput word={word} locale={locale} handleEnter={handleEnter} setWord={setWord} lang={lang}/>
      <Categories lang={lang} locale={locale} allCate={allCate} />
      {/* PCFooter */}
      {!isMobile && <Footer lang={lang} locale={locale} />}
      {/*Footer */}
      {isMobile && <FooterMobile lang={lang} locale={locale} />}
    </div>
  )
})

const injectStore = ({ localeStore: { lang, locale, allCate } }: any) => {
  return {
    lang,
    locale,
    allCate
  }
}

export default inject(injectStore)(Home)