import React, { memo, useMemo, useState, useCallback } from 'react'
import { inject, observer } from 'mobx-react'
import { Link } from 'react-router-dom'
import { ReactComponent as Twitter } from 'src/assets/icon/twitter_icon.svg'
import { ReactComponent as Telegram } from 'src/assets/icon/telegram_icon.svg'
import { ReactComponent as AddIcon } from 'src/assets/icon/add_icon.svg'
import { ReactComponent as MinusIcon } from 'src/assets/icon/minus_icon.svg'
import './style.scss'

interface footerProps {
  lang: string,
  locale: any
}

const FooterMobile: React.FC<footerProps> = observer(({ lang, locale }) => {
  const [height, setHeight] = useState<number>(44)
  const [show, setShow] = useState<any>({ title: '', state: false })

  const freeCheck = useCallback((symbol: any, e: any) => {
    setHeight(e.currentTarget.clientHeight)
    setShow((prevState: any) => prevState?.title === symbol ? ({
      ...prevState,
      state: !prevState.state
    }) : ({
      ...prevState,
      title: symbol,
      state: true
    }))
  }, [])

  const navList = useMemo(() => [
    {
      title: locale.about,
      children: [
        {
          path: window.location.origin + `/article?cate_id=1638430477866&id=1638431526275&lang=${lang}`, label: locale.aboutUs
        },
        {
          path: window.location.origin + `/article?cate_id=1638430552511&id=1638779716968&lang=${lang}`, label: locale.contactUs
        },
        {
          path: window.location.origin + `/?parentId=1638430087856&lang=${lang}`, label: locale.announcement
        },
      ]
    },
    {
      title: locale.product,
      children: [
        { path: window.location.origin + `/article?cate_id=1638517350138&id=1638784676052&lang=${lang}`, label: locale.spotTrading },
        { path: window.location.origin + `/article?cate_id=1638526056906&id=1638785463483&lang=${lang}`, label: locale.combinedStrategy },
        { path: window.location.origin + `/article?cate_id=1638526012500&id=1638785522352&lang=${lang}`, label: locale.trendRotation },
        { path: window.location.origin + `/article?cate_id=1638517662252&id=1638785358983&lang=${lang}`, label: locale.contractTrading }
      ]
    },
    {
      title: locale.service,
      children: [
        { path: window.location.origin + `?lang=${lang}`, label: locale.helpCenter },
        { path: window.location.origin + `/article?cate_id=1638785907543&id=1638785941320&lang=${lang}`, label: locale.FAQ },
        { path: window.location.origin + `/article?cate_id=1638430616225&id=1638781551415&lang=${lang}`, label: locale.TradingStandards }
      ]
    },
    {
      title: locale.terms,
      children: [
        { path: window.location.origin + `/article?cate_id=1638430616225&id=1638779885272&lang=${lang}`, label: locale.tradingRule },
        { path: window.location.origin + `/article?cate_id=1638430616225&id=1638780255487&lang=${lang}`, label: locale.userAgreement },
        { path: window.location.origin + `/article?cate_id=1638430616225&id=1638780105916&lang=${lang}`, label: locale.privacy },
        { path: window.location.origin + `/article?cate_id=1638430616225&id=1638780303792&lang=${lang}`, label: locale.AML },
      ]
    },
  ], [locale, lang])

  const contactUs = useMemo(() => [
    {
      path: 'https://twitter.com/gowcomglobal',
      init_url: <Twitter />
    },
    {
      path: 'https://t.me/gowcom',
      init_url: <Telegram />,
    }
    // {
    //   path: '',
    //   init_url: <Facebook />,
    // },
    // {
    //   path: '',
    //   init_url: <Video />,
    // }
  ], [])

  return (
    <footer className="footer-mobile">
      <ul className="footer-nav">
        {
          navList.map(({ title, children }) => {
            return (
              <li key={title} style={{ height: show.title === title && show.state ? height : 44 }}>
                <div onClick={(e) => freeCheck(title, e)}>
                  <div className="summary">
                    <p>{title}</p>
                    {
                      (show.title === title && show.state) ? <MinusIcon /> : <AddIcon />
                    }
                  </div>
                  <div className="contant">
                    {
                      children.map(({ path, label }) => {
                        if (path.includes('http')) {
                          return <div key={label}>
                            <a href={path} target="_blank" rel="noopener noreferrer" >{label}</a>
                            {label === locale.contractTrading && <span>({locale.comingSoon})</span>}
                          </div>
                        } else {
                          return <div key={label}>
                            <Link key={label} to={path}>{label}</Link>
                            {label === locale.contractTrading && <span>({locale.comingSoon})</span>}
                          </div>
                        }
                      })
                    }
                  </div>
                </div>
              </li>
            )
          })
        }
      </ul>
      <div className="contact-us-text">{lang === 'cn' ? '' : 'Join the community'}</div>
      <div className="contact-us-fooetr">
        {
          contactUs.map(({ path, init_url }: any, index: number) => {
            return <a href={path} key={index}> {init_url}</a>
          })
        }
      </div>
      <div className="copyright">Copyright  2021 GOW LTD All rights reserved.</div>
    </footer>
  )
})

const mapStateToProps = ({ localeStore: { lang, locale } }: any) => {
  return {
    lang,
    locale,
  }
}


export default inject(mapStateToProps)(memo(FooterMobile))
