import React, { useMemo, useState, useCallback, useEffect } from 'react'
import { Link, useHistory, withRouter } from 'react-router-dom'
import { Header, Footer } from 'src/Layout'
import { observer, inject } from 'mobx-react'
import classes from 'classnames'
import { formatTime } from 'src/utils'
import { SearchInput } from 'src/components'
import { ReactComponent as GowICon } from 'src/assets/icon/images/article-gow-logo.svg'
import './PcArticle.scss'

interface PcArticleProps {
  info: {
    title: string,
    updated: string,
    desc: string,
    cateId: number,
    path: string
  }
}

const PcArticle: React.FC<any> = observer(({ locale, lang, props }) => {
  const history = useHistory()
  const [word, setWord] = useState('')
  const [newArticle, setNewArticle] = useState<any>()
  // let arrLen = props.titleDetails?.length
  // let tplMemo='<span className=\'interval-icon\'>></span>'

  useEffect(() => {
    if (props?.curArticle) {
      setNewArticle(props?.curArticle)
    }
  }, [props.curArticle])

  const linkUrl = useMemo(() => [
    {
      name: 'Twitter',
      src: 'https://twitter.com/gowcomglobal'
    },
    // {
    //   name: 'Facebook',
    //   src: 'https://www.facebook.com/huobiglobalofficial/'
    // },
    // {
    //   name: 'Instagram',
    //   src: 'https://www.instagram.com/huobiglobalofficial/'
    // },
    {
      name: 'Telegram',
      src: 'https://t.me/gowcom'
    }
  ], [])

  const handleEnter = useCallback((e) => {
    if (e.keyCode === 13 || e.type === 'click') {
      history.push(`/search?word=${word}&lang=${lang}`)
    }
  }, [history, word, lang])

  const refreshPage = useCallback((article: any) => {
    history.push(`/article?cate_id=${article.cate_id}&id=${article.id}&lang=${lang}`)
    setNewArticle(article)
  }, [history, lang])

  return (
    <div className='pc-article-page'>
      <Header locale={locale} lang={lang} />
      <SearchInput word={word} locale={locale} handleEnter={handleEnter} setWord={setWord} lang={lang} />
      <div className='content-wrap'>
        <div className='article-content-wrap'>
          <div className='left-content'>
            {/*  */}
            <div className='tabs-wrap'>
              <div><Link to='/'><span>{locale.userSupport}</span></Link></div>
              {
                props?.titleDetails?.length && props.titleDetails.map((item: any, index: number) => {
                  if (index === props.titleDetails?.length -1){
                    return (<div key={item.name}><span className='interval-icon'>{'>'}</span><span>{item.name}</span></div>);
                  }else {
                    return (<div key={item.name}>
                      <span className='interval-icon'>{'>'}</span>
                      <Link
                          to={`/?parentId=${item.id}&lang=${lang}`}>
                        <span>{item.name}</span>
                      </Link>
                      </div>)
                  }
                })
              }
            </div>
            <p className='title'>{newArticle?.title}</p>
            <div className='update-time'>
              <GowICon />
              {/* <img src={require('@/assets/icon/images/article-gow-logo.svg')} alt="bg" /> */}
              <div>
                <p>Gow</p>
                <p>{newArticle?.id ? formatTime(newArticle?.id) : ''}</p>
              </div>
            </div>
            <div className='article-content' dangerouslySetInnerHTML={{ __html: newArticle?.rich_text }} />
            {/*  */}
            {
              linkUrl.map(({ name, src }: any) => {
                return <p key={name} className="link-url">{name}
                  <a href={src} target="_blank" rel="noopener noreferrer" >{src}</a>
                  {/* <span>{src}</span> */}
                </p>
              })
            }
          </div>
          <div className='right-content'>
            <p className='result-title'>{locale.relatedArticles}</p>
            {

              props.cateList.map((article: { title: {} | null | undefined; cate_id: any; id: number }) => {
                return (
                  <div key={article?.id} onClick={() => refreshPage(article)}>
                    <p className={classes('article-title', { 'actived-article-title': article.id === newArticle?.id })}>{article.title}</p>
                  </div>
                )
              })
            }
          </div>
        </div>
      </div>
      <Footer lang={lang} locale={locale} />
    </div>
  )
})


const injectStore = ({ localeStore: { locale, lang } }: any, props: PcArticleProps) => {
  return {
    locale,
    lang,
    props
  }
}

export default withRouter(inject(injectStore)(PcArticle))