import React, { memo, useEffect, useMemo, useState } from 'react'
import GEN_API from 'src/api/fetch'
import { Link } from 'react-router-dom'
import { isMobileAgent, parseUrlQuery } from 'src/utils'
import { observer, inject } from 'mobx-react'
import MobileArticle from './MobileArticle'
import PcArticle from './PcArticle'

const ArticlePage: React.FC<any> = observer(({ locale, lang, allCate }) => {
  const query: any = useMemo(() => parseUrlQuery<{ cate_id: string, lang: string, id: string }>(), [])
  const isMobile = isMobileAgent()
  const [cateList, setCateList] = useState<Array<any>>([])
  const [curArticle, setCurArticle] = useState<any>()
  const [titleDetails, setTitleDetails] = useState<any>()
  // let arrLen = titleDetails?.length

  const Tabs = useMemo(() => {
    return (
      <div className='tabs-wrap'>
        <Link to='/'><span>{locale.userSupport}</span></Link>
        {/*<span className='interval-icon'>{'>'}</span>*/}
        {
          titleDetails?.length && titleDetails.map((item: any, index: number) => {
            if (index===titleDetails?.length-1){
              return (<><span className='interval-icon'>{'>'}</span><span>{item.name}</span></>);
            }else {
              return (<><span className='interval-icon'>{'>'}</span>
                <Link
                    key={item.name}
                    to={`/?parentId=${item.id}&lang=${lang}`}>
                  <span>{item.name}</span>
                </Link></>)
            }
          })
        }
      </div>
    )
  }, [locale, titleDetails, lang])

  const articleDetails = useMemo(
    () => {
      if (isMobile) {
        return <>
        <MobileArticle cateList={cateList} curArticle={curArticle} Tabs={Tabs} locale={locale} />
        </>
      } else {
        return <PcArticle cateList={cateList} curArticle={curArticle} titleDetails={titleDetails} />
      }
    }, [cateList, curArticle, isMobile, titleDetails, Tabs, locale])


  useEffect(() => {
    if (curArticle?.title) {
      (async () => {
        const obj = { lang: lang }
        let allInfo: any[] = []
        let item: any = {}
        try {
          const { data } = await GEN_API.getAllCate(obj)
          item = data?.find(({ id }: any) => id === curArticle?.cate_id)
          allInfo.push({ name: item.name, id: item.id })
          let searchRoot = true
          while (searchRoot) {
            if (item.parent_id === 1) {
              searchRoot = false
            } else {
              // eslint-disable-next-line no-loop-func
              item = data?.find(({ id }: any) => id === item?.parent_id)
              allInfo.unshift({ name: `${item?.name}`, id: item?.id })
            }
          }
          setTitleDetails(allInfo)
        } catch (error: any) {
          console.warn(error?.message)
        }
      })()
    }
  }, [lang, allCate, curArticle])

  // 
  useEffect(() => {
    // window.scrollTo(0, 0)
    (async () => {
      try {
        const { data }: any = await GEN_API.queryArticle({ lang: query.lang || 'cn', cate_id: query.cate_id });
        setCateList(data || [])
        if (data?.length) {
          const item = data.filter(({ id }: any) => id === +query.id)
          setCurArticle(item[0] || {})
        }
      } catch (error: any) {
        console.warn(error?.message)
      }
    })()
  }, [query])

  return articleDetails
})
const injectStore = ({ localeStore: { locale, lang, allCate } }: any) => {
  return {
    locale,
    lang,
    allCate,
  }
}

export default inject(injectStore)(memo(ArticlePage))