import { action, observable, makeAutoObservable } from 'mobx'
import enUS from 'src/assets/locales/en_US'
import zhCN from 'src/assets/locales/zh_CN'
import { parseUrlQuery } from 'src/utils'
import { CateData } from 'src/types'
import GEN_API from 'src/api/fetch'

class localeStore {
  constructor() {
    // this.getAllCate('')
    makeAutoObservable(this)
  }
  @observable query = parseUrlQuery<{ lang?: string }>()
  @observable lang: string = this.query.lang || ''
  @observable allCate: CateData[] = []
  @observable locale: any = {}

  @action matchLang = (lang: string) => {
    localStorage.language = lang
    this.lang = lang
    switch (lang) {
      case 'cn':
        this.locale = zhCN
        break;
      case 'en':
        this.locale = enUS
        break;
      default:
        break
    }
    this.getAllCate(lang)
  }

  @action setAllCate = (value: CateData[]) => {
    this.allCate = value
  }

  // 
  private getAllCate = async (lang: string) => {
    const obj ={lang:lang, parent_id:1}
    try {
      const { data } = await GEN_API.getAllCate(obj)
      this.setAllCate(data)
    } catch (error: any) {
      console.warn(error?.message)
    }
  }
}


export default new localeStore()