import React, { ChangeEvent, useCallback } from 'react'
import { ReactComponent as SearchICon } from 'src/assets/icon/icon-search.svg'
import { isMobileAgent } from 'src/utils'
import './SearchInput.scss'

interface SearchInputProps {
  word: string,
  setWord: (value: string) => void,
  locale: any
  handleEnter?: (e: any) => void,
  lang: any
}

const SearchInput: React.FC<SearchInputProps> = ({ word, setWord, locale, handleEnter, lang }) => {
  const isMobile = isMobileAgent()

  const handleChange = useCallback((evt: ChangeEvent<HTMLInputElement>) => {
    setWord(evt.target.value)
  }, [setWord])

  return (
    <div className='search-content'>
      <div className="mobile-content">
        <div className="bg-container">
          {!isMobile && <img src={require('@/assets/icon/images/home-bg-1.png')} alt="bg" />}
          <p>{locale.userSupport}</p>
          {!isMobile && <img src={require('@/assets/icon/images/home-bg-2.png')} alt="bg" />}
        </div>
        <div className='input-wrap'>
          <SearchICon className='search-icon' />
          <input
            value={word}
            type="text"
            placeholder={locale ? locale.searchInputPlaceholder : ''}
            onKeyDown={handleEnter}
            onChange={handleChange}
          />
          {!isMobile && <div className='search-button' onClick={handleEnter}>{lang === 'cn' ? '' : 'Search'}</div>}
        </div>
      </div>
    </div>
  )
}

export default SearchInput