import React, { memo } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { ReactComponent as Service } from 'src/assets/icon/icon-service.svg'
import { ReactComponent as Logo } from 'src/assets/icon/icon-logo.svg'
import { LangSelect } from 'src/components'
import { observer, inject } from 'mobx-react'
import './NavBar.scss'
import { CateData } from 'src/types'

interface NavBarStore extends NavBarProps {
  localeStore: {
    lang: string,
    allCate: CateData[],
    locale: any
  }
}

interface NavBarProps {
  id: number,
  subTab?: string
}

const NavBar: React.FC<any> = observer(({ lang, allCate, locale, props }) => {
  const history = useHistory()
  return (

    <div className={`page-nav-bar-box ${props.subTab ? 'search' : ''}`}>
      <div className='page-nav-bar'>
        <Logo className="nav-logo" onClick={()=>history.push('/')}/>
        <div className='nav-bar-extra'>
          <div className='connect-service'>
            <Link to='/service'>
              <Service className='bar-icon' />
            </Link>
            <LangSelect />
          </div>
        </div>
      </div>
    </div>
  )
})

const injectStore = ({ localeStore: { lang, allCate, locale } }: NavBarStore, props: NavBarProps) => {
  return {
    lang,
    allCate,
    locale,
    props
  }
}

export default inject(injectStore)(memo(NavBar))