import React, { useState, useCallback, useMemo, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { observer, inject } from 'mobx-react'
import { Link } from 'react-router-dom'
// import {RollbackOutlined} from '@ant-design/icons';
// import classes from 'classnames'
import { Pagination } from 'antd';
import GEN_API from 'src/api/fetch'
import { Header, Footer, FooterMobile } from 'src/Layout'
import { NavBar, SearchInput, Empty } from 'src/components'
import { isMobileAgent, parseUrlQuery, formatTime } from 'src/utils'
import { ReactComponent as BackIcon } from 'src/assets/icon/icon-left.svg'
import { UsualData } from 'src/types'
import './Search.scss'


const Search: React.FC<any> = observer(({ lang, locale }) => {
  const history = useHistory()
  const isMobile = isMobileAgent()
  const { word: initWord } = parseUrlQuery<{ word: string }>()
  const [word, setWord] = useState(initWord)
  const [searchWord, setSearchWord] = useState(initWord)
  const [result, setResult] = useState<UsualData[]>([])
  // const [cate, setCate] = useState<null | number | undefined>(null)
  const [total, setTotal] = useState(0)
  const [page, setPage] = useState(1)

  const handleSearch = useCallback((e: any) => {
    console.log(e)
    if (e.keyCode === 13 || e.type === 'click') {
      setSearchWord(word)
      setPage(1)
      history.replace(`/search?word=${word}&lang=${lang}`)
    }
  }, [word, history, lang])


  const onChange = useCallback((pageNumber: any) => {
    console.log('Page: ', pageNumber);
  }, [])

  // id
  // const announcementId = useMemo(() => {
  //   return [
  //     {
  //       lang: 'cn',
  //       id: 11
  //     },
  //     {
  //       lang: 'en',
  //       id: 18
  //     },
  //     // {
  //     //   lang: 'in',
  //     //   id: 24
  //     // },
  //   ]
  // }, [])
  // const types = useMemo(
  //   () => {
  //     return [
  //       {
  //         name: locale.all,
  //         id: null
  //       }, {
  //         name: locale.notice,
  //         id: announcementId.find(item => item.lang === lang)?.id
  //       }
  //     ]
  //   }, [lang, announcementId, locale])

  // const changeType = useCallback(
  //   (item: { name: string, id: number | null | undefined }) => {
  //     return () => {
  //       setCate(item.id)
  //     }
  //   }, [])

  // 
  useEffect(
    () => {
      const getResult = async () => {
        const res = await GEN_API.searchArticle({
          sub_str: searchWord,
          ps: 10,
          pi: page,
          lang: lang,
        })
        if (res?.article_num) {
          setTotal(res?.article_num)
          const result = res.data.map((item: any) => {
            return {
              ...item,
              title: item.title.replace(searchWord, `<span style=color:#0D68EB>${searchWord}</span>`)
            }
          })

          setResult(result)
        } else setResult([])
      }
      getResult()
    }, [searchWord, lang, page, locale])
  const goBackPage = useCallback(() => {
    history.goBack()
  }, [history])
  const articleList = useMemo(
    () => {
      if (isMobile) {
        return result.map((item: UsualData) => {
          return (
            <Link key={item.id} to={`/article?cate_id=${item.cate_id}&id=${item.id}&lang=${lang}`}>
              <div className='result-article'>
                <p>
                  <span dangerouslySetInnerHTML={{ __html: item.title }} />
                  <span className='updated'>{item.id ? formatTime(item.id) : ''}</span>
                </p>
              </div>
            </Link>
          )
        })
      } else {
        return result.map((item: UsualData) => {
          return (
            <Link key={item.id} to={`/article?cate_id=${item.cate_id}&id=${item.id}&lang=${lang}`}>
              <div className='result-article'>
                <span dangerouslySetInnerHTML={{ __html: item.title }} />
                (<span>{item.summary}</span>)
              </div>
            </Link>
          )
        })
      }
    }, [isMobile, result, lang]
  )

  return (
    <div className='search-page-wrap'>
      {!isMobile && <Header locale={locale} lang={lang} />}
      {isMobile && <NavBar subTab={locale.results} />}
      <div className='search-content-wrap'>
        <SearchInput word={word} setWord={setWord} locale={locale} handleEnter={handleSearch} lang={lang} />
        <div className='content-wrap'>
          <div className='content'>
            {
              !isMobile &&
              <div className='tabs'>
                <Link to='/'><span>{locale.userSupport}</span></Link>
                <span className='interval-icon'>{'>'}</span>
                <span>{locale.searchResult}</span>
              </div>
            }
            <div className="results">
              <span onClick={() => goBackPage()}><BackIcon /> {lang === 'en' ? 'Back' : ''}</span>
              <span>
                {lang === 'cn' ? `${searchWord}${locale.resultFor}` : `${locale.resultFor}${searchWord}`} {total} {locale.num}
              </span>
            </div>
            {
              result.length !== 0 && <div className='result-type'>
                {articleList}
              </div>
            }
            {
              result.length !== 0 ? <Pagination defaultCurrent={1} total={total} onChange={onChange} className="search-page"/> : <></>
              // <Pagination
              //   className='paginations'
              //   locale={locale}
              //   total={total}
              //   current={page}
              //   pageSize={10}
              //   onPageChange={setPage}
              // />
            }
            {
              !result.length && <div className='empty-wrapper'><Empty locale={locale} /></div>
            }
            {/*{*/}
            {/*  (!result.length && isMobile) && <FooterMobile lang={lang} locale={locale} />*/}
            {/*}*/}
          </div>
        </div>
      </div>
      {
        !isMobile && <Footer lang={lang} locale={locale} />
      }
      {/*Footer */}
      {isMobile && <FooterMobile lang={lang} locale={locale} />}
    </div>
  )
})

const injectStore = ({ localeStore: { lang, locale, lastestArticle } }: any) => {
  return {
    lang,
    locale,
    lastestArticle
  }
}

export default inject(injectStore)(Search)