import DayJs from 'dayjs'
import { parse as parseQueryString } from 'querystring'


export function parseUrlQuery<T extends object>(): T {
  const { search } = window.location
  return parseQueryString(search.replace('?', '')) as T
}

export function calcFontSize() {
  const html = document.documentElement as HTMLHtmlElement
  const width = html.clientWidth
  const fontSize = 100 * width / 750
  html.setAttribute('style', 'font-size:' + fontSize.toFixed(2) + 'px')
}

export function formatTime(time: number | string | Date, template = 'YYYY-MM-DD HH:mm:ss') {
  if ('number' === typeof time && String(time).length === 10) {
    time = time * 1000
  }
  return DayJs(time).format(template)
}

export const isMobileAgent = (): boolean => {
  if (/(iPhone|iPad|iPod|iOS|Android|Mobile)/i.test(navigator.userAgent)) {
    return true
  } else {
    return false
  }
}

export const parseSearch = (search: string) => {
  if (search) {
    const obj: any = {}
    const parseArr = search.substring(1)?.split('&')
    parseArr.forEach((item: string) => {
      const parseItem = item?.split('=')
      obj[parseItem[0]] = parseItem[1]
    })
    return obj
  }
  return {}
}