import React, {useCallback, useMemo} from 'react'
import { useHistory} from 'react-router-dom'
import { NavBar } from 'src/components'
import { UsualData } from 'src/types'
import './MobileService.scss'
import { RollbackOutlined } from '@ant-design/icons';

interface MobileServiceProps {
  lang: string,
  usual: UsualData[],
  locale: any
}

const MobileService: React.FC<MobileServiceProps> = ({ lang, usual, locale }) => {
  const history = useHistory()
  const features = useMemo(() => {
    return [
      {
        name: locale.beginnerGuide,
        id: 1,
        href: window.location.origin+`/article?cate_id=1638430687270&id=1638781705115&lang=${lang}`
      },
      {
        name: locale.transactionTutorial,
        id: 2,
        href: window.location.origin+`/?parentId=1638517262568&lang=${lang}`
      },
      {
        name: locale.downloadApp,
        id: 3,
        href: 'https://gow.one'
      },
      {
        name: locale.antiFraudGuide,
        id: 4,
        href: window.location.origin+`/article?cate_id=1638430552511&id=1638784511637&lang=${lang}`
      }
    ]
  }, [locale, lang])

  const articleList = useMemo(() => [
    { path: window.location.origin+`/article?cate_id=1638517350138&id=1638784676052&lang=${lang}`, label: locale.spotTrading },
    { path: window.location.origin+`/article?cate_id=1638526056906&id=1638785463483&lang=${lang}`, label: locale.combinedStrategy },
    { path: window.location.origin+`/article?cate_id=1638526012500&id=1638785522352&lang=${lang}`, label: locale.trendRotation },
    { path: window.location.origin+`/article?cate_id=1638517662252&id=1638785358983&lang=${lang}`, label: locale.contractTrading }
  ], [lang, locale])

  const goBackPage = useCallback(() => {
    history.goBack()
  },[history])
  return (
    <div className='mobile-service-page-wrap'>
      <NavBar />
      <div className="service-title">
        <span>{locale.onlineService}</span>
        <span onClick={() => goBackPage()}><RollbackOutlined /></span>
      </div>
      <div className="serve-box">
        <div className="feature-box">
          <div className='main-feature'>
            {
              features.map(item => {
                return (
                    <a href={item.href} key={item.id} target="_blank" rel="noopener noreferrer" >{item.name}</a>
                )
              })
            }
          </div>
          <div className="article">
            {
              articleList.map(({ path, label }: any) => {
                return <a href={path} target="_blangk" rel="noopener noreferrer" >{label}</a>
              })
            }
          </div>
        </div>
        <div className="container tips">{locale.warnInfo}</div>
        <div className="container WhatAPP">
          <p>{locale.serviceLine}</p>
          <p>
            <span>{locale.serviceTime}</span><br />
            <a href="https://t.me/gowcom" target="_blank" rel="noopener noreferrer" >https://t.me/gowcom</a><br />
            <span>{locale.email}support@gow.com</span>
          </p>
          <p>{locale.couldGet}</p>
          <p style={{ whiteSpace: 'pre-line' }}>{locale.services}</p>
        </div>
      </div>
    </div>
  )
}

export default MobileService