import React, { useMemo, useCallback, useState, useEffect, memo } from 'react'
import { useHistory, withRouter } from 'react-router-dom'
import classes from 'classnames'
import { observer, inject } from 'mobx-react'
import { ReactComponent as LangIcon } from 'src/assets/icon/icon-lang.svg'
import { parseUrlQuery, isMobileAgent } from 'src/utils'
import './LangSelect.scss'

const LangSelect: React.FC<any> = observer(({ lang, locale, matchLang }) => {
  const history = useHistory()
  const isMobile = isMobileAgent()
  const query = parseUrlQuery<any>()
  const [lan, setLan] = useState(lang)
  const [label, setLabel] = useState('')
  const [show, setShow] = useState(false)

  const langList = useMemo(() => [
    {
      label: 'English',
      value: 'en'
    },
    {
      label: '',
      value: 'cn'
    },
  ], [])
    const handleShow = useCallback(
        () => {
            setShow(prev => !prev)
        }, []
    )
    function clickCallback() {
        setShow(false);
    }
    useEffect(() => {
        if (show) {
            document.addEventListener("click", clickCallback, false);
            return () => {
                document.removeEventListener("click", clickCallback, false);
            };
        }
    }, [show]);

  useEffect(() => {
    const list = langList?.filter(({ value }: any) => value === lang)
    list.length ? setLabel(list[0].label) : setLabel('')
  }, [lang, langList])

  useEffect(() => {
    setLabel(() => (langList.find(item => item.value === lan)?.label || ''))
  }, [lan, langList])

  const selectLan = useCallback(
    (item: { label: string, value: string }) => {
      if (query.lang) {
        history.replace(`${history.location.pathname}${history.location.search}`.replace(`lang=${query.lang}`, `lang=${item.value}`))
      }
      matchLang(item.value)
      setLan(item.value)
      setLabel(item.label)
      setShow(false)
      history.go(0)
    }, [query.lang, history, matchLang])

  return (
    <div className='lang-select-wrap'>
      <div onClick={handleShow}>
       {isMobile &&  <LangIcon className='icon-lang' />}
       {/* <LangIcon className='icon-lang' /> */}
        {
          !isMobile && <span>{label}</span>
        }
      </div>
      {
        show &&
        <div className='select-content'>
          {
            langList.map(item => {
              return (
                <p
                  key={item.value}
                  onClick={() => selectLan(item)}
                  className={classes({ 'active-lan': item.value === lang })}>
                  {item.label}
                </p>
              )
            })
          }
        </div>
      }
    </div>
  )
})


const injectStore = ({ localeStore: { lang, locale, matchLang } }: any) => {
  return {
    lang,
    locale,
    matchLang
  }
}

export default withRouter(inject(injectStore)(memo(LangSelect)))