import React, { useMemo } from 'react'
import { Link } from 'react-router-dom'
import { Header, Footer } from 'src/Layout'
import { ChatMessage } from 'src/components'
import { UsualData } from 'src/types'
import './PcService.scss'

interface PcServiceProps {
  lang: string,
  usual: UsualData[],
  locale: any
}

const PcService: React.FC<PcServiceProps> = ({ lang, usual, locale }) => {
  const messageFirst = useMemo(
    () => {
      return (
        <>
          <p>{locale.canHelp}</p><br />
          <p><a style={{ color: '#171b21' }} href="https://t.me/gowcom">{locale.addApp}</a></p><br />
          <a href="https://gow.one"><p style={{ color: '#0D68EB' }}>{locale.fetchMore}</p><br /></a>
          <p>
            {locale.warnInfo}
            <Link to={`/article/104?path=619d9b4afb653400014f7f87&lang=${lang}`}><span style={{ color: '#0D68EB' }}>{locale.antiFraudGuide}</span></Link>
          </p>
        </>
      )
    }, [locale, lang])
  const messageSecond = useMemo(
    () => {
      return (
        <>
          <p>{locale.serviceLine}</p>
          <p><a style={{ color: '#0D68EB' }} href="https://t.me/gowcom">https://t.me/gowcom</a>({locale.clickAdd})</p><br />
          <p>{locale.email}support@gow.com</p><br />
          <p>{locale.couldGet}</p>
          <p style={{ whiteSpace: 'pre-line' }}>{locale.services}</p>
        </>
      )
    }, [locale])

  const features = useMemo(() => {
    return [
      {
        name: locale.beginnerGuide,
        id: 1,
        path: `article/103?path=619c516dfb653400014f7f86&lang=${lang}`
      },
      {
        name: locale.transactionTutorial,
        id: 2,
        path: `article/4?lang=${lang}&path=61823170fb65340001816003`
      },
      {
        name: locale.downloadApp,
        id: 3,
        path: 'https://gow.one'
      },
      {
        name: locale.antiFraudGuide,
        id: 4,
        path: `/article/104?path=619d9b4afb653400014f7f87&lang=${lang}`
      }
    ]
  }, [locale, lang])
  return (
    <div className='service-page-wrap'>
      <Header locale={locale} lang={lang} />
      <div className='container-wrap'>
        <div className='content'>
          <div className='tabs'>
            <Link to={`/`}>
              <span>{locale.userSupport}</span>
            </Link>
            <span className='interval-icon'>{'>'}</span>
            <Link to={`/category/15?path=612481c13c15c200018afd63&lang=${lang}`}>
              <span>{locale.noviceTutorials}</span>
            </Link>
            <span className='interval-icon'>{'>'}</span>
            <span>{locale.userGuide}</span>
          </div>
          <div className='chat-wrap'>
            <div className='chat'>
              <div className='title'>
                <span>{locale.onlineService}</span>
                <span>{locale.personCustomerService}</span>
              </div>
              <div className='content-wrap'>
                <div className='left'>
                  <div className='receive-message'>
                    <ChatMessage>
                      {messageFirst}
                    </ChatMessage>
                    <ChatMessage>
                      {messageSecond}
                    </ChatMessage>
                    <div className='main-feature'>
                      {
                        features.map(item => {
                          if (item.id === 3) {
                            return (
                              <a href={item.path} key={item.id} >
                                <span key={item.id}>{item.name}</span>
                              </a>
                            )
                          } else {
                            return (
                              <Link to={item.path} key={item.id} >
                                <span>{item.name}</span>
                              </Link>
                            )
                          }
                        })
                      }
                    </div>
                  </div>
                  <div className='send-message'>
                    <span>{locale.send}</span>
                  </div>
                </div>
                <div className='right'>
                  <p className='article-title'>{locale.relatedArticles}</p>
                  {
                    usual.map(item => {
                      return (
                        <Link key={item.id} to={`/article/129?lang=${lang}&path=${item.path}`}>
                          <p className='article-name'>{item.title}</p>
                        </Link>
                      )
                    })
                  }
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer lang={lang} locale={locale} />
    </div>
  )
}


export default PcService