import { useState, useCallback, useMemo } from 'react'
const useToggle = () => {
  const [current, setToggle] = useState<boolean>(false)
  const update = useCallback(() => {
    setToggle(prevState => !prevState)
  }, [])
  return useMemo(() => ({
    update,
    current
  }), [update, current])
}

export default useToggle