import React, { useMemo, useState, useEffect } from 'react'
import { observer, inject } from 'mobx-react'
import PcService from './PcService'
import MobileService from './MobileService'
// import GEN_API from 'src/api/fetch'
import { UsualData } from 'src/types'
import { isMobileAgent } from 'src/utils'


const Service: React.FC<any> = observer(({ lang, locale }) => {
  console.log('')
  const isMobile = isMobileAgent()
  const [usual, setUsual] = useState<UsualData[]>([])
  const content = useMemo(
    () => {
      return isMobile
        ? <MobileService lang={lang} locale={locale} usual={usual} />
        : <PcService lang={lang} locale={locale} usual={usual} />
    }, [isMobile, locale, usual, lang])

  useEffect(() => {
    if (lang) {
      (async () => {
        try {
          // const { data }: any = await GEN_API.fetUsualQuestions(lang, { ps: 5 });
          setUsual([])
        } catch (err: any) {
          console.log('', err)
        }
      })()
    }
  }, [lang])

  return content
})


const injectStore = ({ localeStore: { lang, locale } }: any) => {
  return {
    lang,
    locale
  }
}

export default inject(injectStore)(Service)