import React, { useCallback, useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { formatTime, parseUrlQuery, isMobileAgent } from 'src/utils'
// import { FooterMobile } from 'src/Layout'
import { Pagination } from 'antd';
import GEN_API from 'src/api/fetch'
// import { CateData, UsualData } from 'src/types'
import { CateData } from 'src/types'
import './Categories.scss'
import { Empty } from 'antd';

const Categories: React.FC<any> = ({ lang, locale, allCate, history }) => {
  const isMobile = isMobileAgent()
  const [usual, setUsual] = useState<CateData[]>([])
  const [parentId, setParentId] = useState<number>() // 
  const [curUsual, setCurUsual] = useState<any>()// 
  const [articleList, setArticleList] = useState<any>()
  const [pageInfo, setPageInfo] = useState<any>({ num: 0, size: 10 })

  // 
  const getUsual = useCallback(async (parent_id: number) => {
    setParentId(+parent_id)
    try {
      const { data }: any = await GEN_API.getAllCate({ lang, parent_id });
      if (data?.length) {
        setUsual(data) //
        setCurUsual(data[0])
      } else {
        setUsual([])
      }
    } catch (err: any) {
      console.log('', err)
    }
  }, [lang])

  useEffect(() => {
    if (allCate?.length) {
      const query = parseUrlQuery<any>()
      let id = query.parentId ? query.parentId : allCate[0]?.id
      getUsual(id)
    }
  }, [allCate, getUsual, history])

  const onChange = useCallback((pageNumber: any) => {
    console.log('Page: ', pageNumber);
  }, [])


  //
  useEffect(() => {
    if (curUsual) {
      (async () => {
        try {
          const item: any = await GEN_API.queryArticle({ lang, cate_id: curUsual.id });
          if (item.code!==0){
            setArticleList(item.data || [])
            setPageInfo(() => ({ num: item?.page_num, size: item?.page_size }))
          }else {
            setArticleList('')
            setPageInfo(() => ({ num: 0, size: 0 }))
          }
        } catch (err: any) {
          console.log('', err)
        }
      })()
    }
  }, [lang, curUsual])

  return (
    <div className='categories-wrap'>
      <div className='categories-content'>
        {
          allCate?.map(({ name, id }: any) => {
            return <div className={parentId === id ? 'active' : ''} onClick={() => getUsual(id)} key={name}>{name}</div>
          })
        }
      </div>
      <div className="question-contain">
        {/*  */}
        <ul className="directory">
          {
            usual?.map((item: any) => {
              return <li className={curUsual?.id === item.id ? 'active' : ''} key={item.name} onClick={() => setCurUsual(item)}>{item.name}</li>
            })
          }
        </ul>
        <div className="details">
          {/* */}
          {!isMobile && <div className="title">{curUsual?.name}</div>}
          <ul className="text">
            {
              Array.isArray(articleList) ? <>
                {
                  articleList.map((item: any) => {
                    return (
                      <Link key={item?.title} to={`/article?cate_id=${item.cate_id}&id=${item.id}&lang=${lang}`}>
                        <li>
                          <p>{item?.title}</p>
                          <span>{formatTime(item?.id)}</span>
                        </li>
                      </Link>
                    )
                  })
                }
              </> : <>{
                lang==='cn'? <Empty description='' />:<Empty description='No data temporarily' />
              }</>
              // </> : <div>{articleList || ''}</div>
            }
          </ul>
          {
            pageInfo?.num!==0 ? <Pagination showQuickJumper defaultCurrent={1} total={pageInfo?.num} onChange={onChange} /> : <></>
          }
        </div>
      </div>
    </div >
  )
}

export default withRouter(Categories)